import React from "react";
import ReactToPrint from "react-to-print";

import Bowser from "bowser";

import { Button, Container, CssBaseline, Hidden } from "@material-ui/core";

import { createMuiTheme, createStyles, makeStyles, responsiveFontSizes } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";

import PrintIcon from "@material-ui/icons/Print";

import data from "./data.json";
import Profile from "./Profile";
import theme from "./theme.json";

const useStyles = makeStyles(theme =>
    createStyles({
        printButton: {
            backgroundColor: "whitesmoke",
            margin: theme.spacing(1),
        },
        printContainer: {
            marginBottom: theme.spacing(-2),
            padding: theme.spacing(1, 0, 0, 3),
        },
        root: {
            padding: theme.spacing(3),
        },
    })
);

const browser = Bowser.getParser(window.navigator.userAgent);
console.log(browser);

const App = function(props) {
    const classes = useStyles(props);

    const [printing, setPrinting] = React.useState(false);
    const rootRef = React.useRef();

    return (
        <>
            {/chrome/i.test(browser.getBrowserName()) && (
                <ReactToPrint
                    trigger={() => (
                        <Container className={classes.printContainer} maxWidth="lg">
                            <Hidden only={["xs"]}>
                                <Button
                                    variant="contained"
                                    color="default"
                                    className={classes.printButton}
                                    startIcon={<PrintIcon />}
                                >
                                    Print
                                </Button>
                            </Hidden>
                        </Container>
                    )}
                    content={() => rootRef.current}
                    onBeforeGetContent={() => {
                        setPrinting(true);
                    }}
                    onAfterPrint={() => {
                        setPrinting(false);
                    }}
                />
            )}
            <div ref={rootRef}>
                <ThemeProvider theme={responsiveFontSizes(createMuiTheme(theme))}>
                    <CssBaseline />
                    <Container className={classes.root} maxWidth="lg">
                        <Profile printing={printing} resume={data} />
                    </Container>
                </ThemeProvider>
            </div>
        </>
    );
};

export default App;
