import React from "react";

import { createStyles, makeStyles } from "@material-ui/core/styles";

import WordCloud from "react-d3-cloud";

import links from "./links";

const skills = [
    "Python",
    "JavaScript",
    "React",
    "Django",
    "Celery",
    "Material Design",
    "Docker",
    "Memcached",
    "Cypress",
    "PostgreSQL",
    "AWS",
    "Redis",
    "Groovy",
    "Java",
    "Vue",
    "Node.js",
    "TypeScript",
    "GraphQL",
    "Objective-C",
    "iOS",
    "Android",
    "Ruby",
    "Rails",
    "Grails",
    "Scala",
    "Spring",
    "Hibernate",
    "Drools",
    "D3",
    "Nginx",
    "MongoDB",
    "Elasticsearch",
    "MySQL",
    "Spark",
    "Logstash",
    "Ansible",
    "Maven",
    "Gradle",
    "Ant",
    "Sentry",
];

const useStyles = makeStyles(theme =>
    createStyles({
        root: {
            "& text": {
                cursor: "pointer",
            },
        },
    })
);

export default function(props) {
    const classes = useStyles(props);

    return (
        <div className={classes.root}>
            <WordCloud
                data={skills.reverse().map((item, idx) => {
                    return {
                        text: item,
                        value: idx + 1,
                    };
                })}
                height={400}
                font="Impact"
                fontSizeMapper={word => Math.log2(word.value) * 8}
                padding={2}
                onWordClick={word => {
                    const link = links[word.text];
                    if (link) {
                        window.open(link, "_blank");
                    }
                }}
                rotate={word => ~~(Math.random() * 2) * 90}
            />
        </div>
    );
}
