import React from "react";

import { Avatar, Grid, Hidden, Link, Tooltip, Typography } from "@material-ui/core";

import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import { IconType } from "react-icons";
import { FaGithub, FaLinkedin, FaNewspaper, FaStackOverflow, FaSkull, FaThumbtack, FaTwitter } from "react-icons/fa";

import { Contact } from "./data";
import FlagIcon from "./FlagIcon";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        avatar: {
            height: 56,
            width: 56,
        },
        language: {
            cursor: "pointer",
            padding: theme.spacing(1),
        },
        personality: {
            height: 21,
            width: 21,
        },
    })
);

const linkIcons: { [key: string]: IconType } = {
    Github: FaGithub,
    Instapaper: FaNewspaper,
    LinkedIn: FaLinkedin,
    Personality: FaSkull,
    Pinboard: FaThumbtack,
    StackOverflow: FaStackOverflow,
    Twitter: FaTwitter,
};

interface Props {
    contact: Contact;
}

export default function(props: Props) {
    const classes = useStyles(props);
    const { contact } = props;

    return (
        <Grid container justify="space-between" spacing={2}>
            <Hidden only={["xs"]}>
                <Grid item sm={8}>
                    <Grid container spacing={2}>
                        <Grid item>
                            <Avatar alt={contact.name} src="/logo.png" className={classes.avatar} />
                        </Grid>
                        <Grid item>
                            <Grid container direction="column">
                                <Grid item>
                                    <Typography variant="h5">{contact.name}</Typography>
                                </Grid>
                                <Grid item>
                                    <Grid container spacing={1}>
                                        {Object.entries(contact.links).map(([key, value]) => {
                                            const Icon = linkIcons[key];
                                            return (
                                                <Grid item key={key}>
                                                    <Tooltip title={key}>
                                                        <Link
                                                            href={value}
                                                            color="textSecondary"
                                                            variant="body2"
                                                            target="_blank"
                                                            rel="noopener"
                                                        >
                                                            <Icon size="1.5em" />
                                                        </Link>
                                                    </Tooltip>
                                                </Grid>
                                            );
                                        })}
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Hidden>
            <Grid item xs={12} sm={4}>
                <Grid container direction="column" alignItems="flex-end" spacing={1}>
                    <Grid item>
                        {contact.languages.map(item => (
                            <Tooltip key={item.code} title={item.level}>
                                <span className={classes.language}>
                                    <FlagIcon code={item.code} size="lg" />
                                </span>
                            </Tooltip>
                        ))}
                    </Grid>
                    <Grid item>
                        <Link href={`mailto:${contact.email}`} variant="body2" target="_blank" rel="noopener">
                            {contact.email}
                        </Link>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    );
}
