import React from "react";

import { Divider, Grid, Hidden, Paper, Typography } from "@material-ui/core";

import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import {
    FaBasketballBall,
    FaBookOpen,
    FaChessKnight,
    FaDice,
    FaFilm,
    FaLaptop,
    FaMusic,
    FaOdnoklassniki,
    FaPlane,
    FaTv,
} from "react-icons/fa";

import { Resume } from "./data";

import Contact from "./Contact";
import Education from "./Education";
import Experience from "./Experience";
import RadarChart from "./RadarChart";
import TagCloud from "./TagCloud";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: theme.spacing(3),
        },
    })
);

const icons = {
    summary: ["👨🏻‍✈️", "👷🏻‍♂️", "🧙🏻‍♂️", "👨🏻‍🎨", "👨🏻‍💻"],
    experience: ["🔍", "📊", "🏆", "📚", "🏛", "🧰", "🌐", "🖥"],
    education: ["🎓", "🎓"],
    hobbies: [
        FaLaptop,
        FaPlane,
        FaMusic,
        FaOdnoklassniki,
        FaBasketballBall,
        FaFilm,
        FaTv,
        FaDice,
        FaChessKnight,
        FaBookOpen,
    ],
};

interface Props {
    printing: boolean;
    resume: Resume;
}

const DT_STARTED_CAREER = "2003-01-01";
const MS_PER_DAY = 1000 * 60 * 60 * 24;

function getDiffInYears(since: Date, then?: Date) {
    then = then || new Date();
    const utc1 = Date.UTC(since.getFullYear(), since.getMonth(), since.getDate());
    const utc2 = Date.UTC(then.getFullYear(), then.getMonth(), then.getDate());
    const diffInDays = Math.floor((utc2 - utc1) / MS_PER_DAY);
    return Math.ceil(diffInDays / 365);
}

export default function(props: Props) {
    const classes = useStyles(props);
    const { printing, resume } = props;

    resume.summary.forEach((item, idx) => {
        if (/N\+/.test(item)) {
            const years = getDiffInYears(new Date(DT_STARTED_CAREER));
            resume.summary[idx] = item.replace("N+", `${years}+`);
        }
    });

    return (
        <Paper className={classes.root}>
            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Contact contact={resume.contact} />
                </Grid>
                <Grid item xs={12}>
                    <Divider />
                </Grid>
                <Grid item xs={12}>
                    {resume.summary.map((item, idx) => (
                        <Typography
                            gutterBottom
                            key={idx}
                            variant="body1"
                        >{`${icons.summary[idx]} ${item}`}</Typography>
                    ))}
                </Grid>
                <Grid item xs={12}>
                    {resume.experience.map((item, idx) => (
                        <Experience key={idx} experience={item} emoji={icons.experience[idx]} />
                    ))}
                </Grid>
                <Grid item xs={12}>
                    <Divider />
                </Grid>
                <Grid item xs={12}>
                    <Grid container spacing={1}>
                        {resume.education.map((item, idx) => (
                            <Grid item key={idx} xs={12}>
                                <Education education={item} emoji={icons.education[idx]} />
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
                {!printing && (
                    <Hidden only={["xs"]}>
                        <Grid item sm={12}>
                            <Divider />
                        </Grid>
                        <Grid item sm={12}>
                            <Grid container justify="space-between">
                                <Grid item sm={12} lg={7}>
                                    <TagCloud />
                                </Grid>
                                <Grid item sm={12} lg={5}>
                                    <RadarChart />
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item sm={12}>
                            <Divider />
                        </Grid>
                        <Grid item sm={12}>
                            <Grid container spacing={2}>
                                {resume.hobbies.map((item, idx) => {
                                    const Icon = icons.hobbies[idx];
                                    return (
                                        <Grid item key={idx}>
                                            <Grid container spacing={1}>
                                                <Grid item>
                                                    <Icon size="1.2rem" />
                                                </Grid>
                                                <Grid item>
                                                    <Typography variant="caption">{item}</Typography>
                                                </Grid>
                                            </Grid>
                                        </Grid>
                                    );
                                })}
                            </Grid>
                        </Grid>
                    </Hidden>
                )}
            </Grid>
        </Paper>
    );
}
