import React from "react";

import { Chip, Divider, Grid, Hidden, Link, Typography } from "@material-ui/core";

import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import { Experience } from "./data";
import links from "./links";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        bold: {
            fontWeight: 500,
        },
        root: {
            paddingBottom: theme.spacing(2),
        },
    })
);

interface Props {
    experience: Experience;
    emoji: string;
}

export default function(props: Props) {
    const classes = useStyles(props);
    const { experience, emoji } = props;

    return (
        <Grid container className={classes.root} spacing={1}>
            <Grid item xs={12}>
                <Divider />
            </Grid>
            <Grid item xs={12}>
                <Grid container spacing={1}>
                    <Grid item>
                        <Typography className={classes.bold}>{`${emoji} ${experience.title}`}</Typography>
                    </Grid>
                    <Grid item>
                        <Typography className={classes.bold}>–</Typography>
                    </Grid>
                    <Grid item>
                        <Link href={links[experience.company]} target="_blank" rel="noopener">
                            <Typography className={classes.bold}>{experience.company}</Typography>
                        </Link>
                    </Grid>
                    <Grid item>
                        <Typography variant="body1">{experience.location}</Typography>
                    </Grid>
                    <Grid item>
                        <Typography variant="body1">{experience.period.join(" – ")}</Typography>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12}>
                {experience.description.map((item, idx) => (
                    <Typography gutterBottom key={idx} variant="body1" component="li">
                        {item}
                    </Typography>
                ))}
            </Grid>
            <Hidden only={["xs"]}>
                <Grid item md={12}>
                    <Grid container spacing={1}>
                        {experience.technologies.map((item, idx) => (
                            <Grid item key={idx}>
                                <Chip
                                    label={item}
                                    size="small"
                                    variant="outlined"
                                    onClick={() => {
                                        const url = links[item];
                                        window.open(url, "_blank");
                                    }}
                                />
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
            </Hidden>
        </Grid>
    );
}
