import React from "react";

import Highcharts from "highcharts";
import HighchartsMore from "highcharts/highcharts-more";
import HighchartsReact from "highcharts-react-official";

HighchartsMore(Highcharts);

export default function() {
    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={{
                chart: {
                    polar: true,
                    type: "line",
                },
                title: {
                    text: null,
                    x: 0,
                },
                xAxis: {
                    categories: [
                        "Algorithms",
                        "APIs",
                        "Architecture",
                        "Databases",
                        "Design patterns",
                        "Frameworks",
                        "Infrastructure",
                        "Programming Languages",
                        "Mobile development",
                        "Security",
                        "UI/UX design",
                        "Web development",
                    ],
                    labels: {
                        style: {
                            color: "#222",
                        },
                    },
                    gridLineColor: "#ccc",
                    tickmarkPlacement: "on",
                    lineWidth: 0,
                },
                yAxis: {
                    gridLineColor: "#ccc",
                    gridLineInterpolation: "polygon",
                    lineWidth: 0,
                    max: 5,
                    min: 0,
                    labels: {
                        enabled: false,
                    },
                },
                series: [
                    {
                        name: "Skills",
                        data: [
                            3.0, // Algorithms
                            4.5, // APIs
                            4.5, // Architecture
                            3.0, // Databases
                            4.0, // Design patterns
                            4.5, // Frameworks
                            3.5, // Infrastructure
                            4.5, // Languages
                            2.5, // Mobile development
                            3.5, // Security
                            4.5, // UI/UX design
                            4.5, // Web development
                        ],
                        pointPlacement: "on",
                    },
                ],
                pane: {
                    size: "75%",
                },
                plotOptions: {
                    series: {
                        tooltip: {
                            pointFormatter: function() {
                                const value = this.y;
                                let rank = "N/A";
                                if (value <= 1) {
                                    rank = "Novice";
                                } else if (value <= 2) {
                                    rank = "Beginner";
                                } else if (value <= 3) {
                                    rank = "Intermediate";
                                } else if (value <= 4) {
                                    rank = "Advanced";
                                } else {
                                    rank = "Expert";
                                }
                                return `<b>${rank}</b>`;
                            },
                        },
                    },
                },
                legend: {
                    enabled: false,
                },
                credits: {
                    enabled: false,
                },
                exporting: {
                    enabled: false,
                },
            }}
        />
    );
}
